.main {
  width: 100%;
  margin-bottom: 90px;

  &__slider {
    width: 100%;
  }
}

.swiper-container {
  width: 100%;
}

.swiper-wrapper {
  width: 100%;
}

.swiper-slide {
  width: 100%;
}

.slider__background {
  width: 100%;
  height: 100%;
  background-image: url("../images/jpg/main-slider/main-slider-background.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.slider__container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  justify-content: space-between;

}

.slider__content {
  margin: 100px 0;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;

  &--title {
    margin: 0;
    font-size: 72px;
    font-weight: 900;
    line-height: 86px;
    color: white;
    &-sub {
      font-size: 54px;
      font-weight: 900;
      line-height: 65px;
      color: white;
    }
  }

  &--button {
    width: 239px;
    height: 42px;
    background-color: #f79900;
    margin-bottom: 38px;
    @extend %flexCenter;
    text-decoration: none;

    &-text {
      margin: 0;
      font-size: 24px;
      font-weight: 400;
      line-height: 27px;
      color: black;
    }
  }

  &--paragraph {
    font-size: 14px;
    font-weight: 400;
    line-height: 22px;
    color: white;
  }
}
