.footer {

  &__background {
    width: 100%;
    height: 100%;
    background-image: url("../images/jpg/footer/footer-background.jpg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }

  &__body {
    display: flex;
    flex-direction: column;
    margin: 75px 0;
    @media(max-width: 920px) {
      margin: 0;
    }
  }

  &__nav {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    margin-bottom: 28px;
    @media(max-width: 920px) {
      grid-template-columns: repeat(2, 1fr);
      justify-items: center;
      align-items: start;
      align-content: start;
      justify-content: start;
    }

    @media(max-width: 468px) {
      grid-template-columns: repeat(1, 1fr);
    }
  }

  &__logo {
    &-image {
      width: 191px;
      padding: 5px;
      transition: transform 0.3s ease;

      &:hover {
        transform: scale(1.2);
      }
    }
  }
}

.nav-block {
  max-width: 250px;

  &__title {
    margin-bottom: 17px;

    @media(max-width: 920px) {
      margin: 0;
    }
    @media(max-width: 468px) {
      margin-bottom: 15px;
      text-align: center;
      transition: color 0.3s ease;
      &:hover {
        color: #f79a00;
      }
    }

    &-text {
      margin: 0;
      font-size: 18px;
      font-weight: 900;
      line-height: 27px;
      text-align: left;
      color: white;
      background-color: inherit;
      border: none;
      outline: none;
      padding: 0;
    }
  }

  &__list {
    padding: 0;
    @media(max-width: 468px) {
      display: none;
      overflow: hidden;
      transition: display 0.3s ease;
      text-align: center;
    }

    &--item {
      list-style: none;
      margin: 7px 0;

      &-link {
        padding: 10px 0;
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: 28px;
        text-align: left;
        color: white;
        text-decoration: none;

        &:hover {
          transition: color 0.3s ease;
          color: #f79a00;
        }
      }
    }
  }

  &__contacts {
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    &--number {
      font-size: 18px;
      font-weight: 900;
      line-height: 27px;
      color: white;
      text-decoration: none;
      transition: color 0.3s ease;
      margin-bottom: 20px;
      &:hover {
        color: #f79a00;
      }
    }
  }

  &__soNets {
    padding: 3px 0;
    display: flex;
    justify-content: flex-start;
    text-align: left;
    width: 181px;
    gap: 7px;

    @media(max-width: 468px) {
      justify-content: center;
    }

    &--logo {
      text-decoration: none;
      transition: transform 0.3s ease;
      border-radius: 50px;

      &:hover {
        transform: scale(1.2);
      }

      &-image {
        border-radius: 50%;
        transition: box-shadow 0.3s ease;

        &:hover {
          box-shadow: 0 0 10px #f79a00;
        }
      }
    }
  }
}

.contacts {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  @media(max-width: 920px) {
    align-items: flex-start;
  }
}
