._container {
  max-width: $maxWidthContainerHD + px;
  padding: 15px 20px;
  margin: 0 auto;
  @media (min-width: 1920px) {
    max-width: $maxWidthContainerHD + px;
  }
}

.header {
  z-index: 2;
  box-sizing: border-box;
  background-color: white;
  height: 82px;
  &__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__main {
    z-index: 5;
    display: flex;
    align-items: center;
    gap: 20px;
    &--burger {
      display: none;
      width: 20px;
      height: 20px;
      @media (max-width: 992px) {
        display: block;
      }
    }
  }
}

.logo_wrapper {
  width: 194px;
  height: 38px;
  padding: 5px;
  @extend %flexCenter;
  transition: all 0.3s ease;

  &:hover {
    border-radius: 15px;
    box-shadow: 0 0 10px #f79a00;
    transform: scale(1.1);
  }
}

.logo {
  z-index: 5;
  max-width: 197px;
  height: 36px;
  background-repeat: no-repeat;
}

.nav {
  z-index: 3;
  width: 70%;
  @media(max-width: 767px) {
    position: fixed;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100vh;
    background: white;

    visibility: hidden;
    transition: all 0.3s ease;
    &.active {
      padding: 64px 0 0;
      left: 0;
      visibility: inherit;
      overflow: hidden;
      justify-content: center;
    }
  }

  &__list {
    list-style: none;
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 0;
    margin: 0;
    @media(max-width: 767px) {
      height: 90%;
      flex-direction: column;
      justify-content: space-around;
      align-items: center;
    }
  }
  &__item {
    font-family: "Gotham Pro", sans-serif;
    font-weight: 400;
    font-size: 14px;
    line-height: 13.4px;
    padding: 15px;
  }
  &__link {
    text-decoration: none;
    color: black;
    transition: color 0.3s ease;
    &:hover {
      color: #f79a00;
    }
    @media (max-width: 768px) {
      font-size: 15px;
      font-weight: 600;
      line-height: 20px;
    }

    @media(min-width: 1920px) {
      font-size: 20px;
      line-height: 35px;
    }
  }
}

.burger {
  z-index: 5;
  display: none;
  @media (max-width: 767px) {
    display: block;
  }
  position: relative;
  width: 20px;
  height: 20px;
  &__icon {
    position: absolute;
    background-color: #727786;
    left: 0;
    width: 100%;
    height: 2px;
    top: 9px;
    border-radius: 5px;
    transition: transform 0.5s ease;
  }
  &:before,
  &:after {
    content: "";
    background-color: black;
    position: absolute;
    width: 100%;
    height: 2px;
    left: 0;
    border-radius: 5px;
    transition: transform 0.5s ease;
  }
  &:before {
    background-color: #8c91a0;
    top: 0;
  }
  &:after {
    background-color: #595e6d;
    bottom: 1px;
  }
  &.active:before {
    transform: rotate(45deg);
    top: 9px;
  }
  &.active:after {
    transform: rotate(-45deg);
    bottom: 9px;
  }
  &.active &__icon {
    transform: scale(0);
  }
}
