.main__info {
  margin: 90px 0;

  &--container {
    display: flex;
    flex-direction: column;
  }

  &--title {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    gap: 100px;
    margin-bottom: 90px;

    &-content {
      display: flex;
      flex-direction: column;
      justify-content: center;

    }
  }
}

.title {
  margin: 0 0 35px;
  font-size: 48px;
  font-weight: 900;
  line-height: 58px;

  &-colored {
    color: #f79a00;
  }
}

.sub-title {
  font-size: 18px;
  font-weight: 400;
  line-height: 18px;
  color: #7d7d7d;

}

.numbers-colored {
  font-size: 18px;
  font-weight: 900;
  line-height: 18px;
  color: #f79a00;
}

.main__info--blocks {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  margin-bottom: 90px;
}

.info-block {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  color: white;

  &__content {
    padding: 58px 36px 48px;
  }

  &__title {
    font-size: 18px;
    font-weight: 900;
    line-height: 22px;
    margin: 0 0 19px;
  }

  &__about {
    font-size: 12px;
    font-weight: 400;
    line-height: 17px;
    margin: 0 0 15px;
  }

  &__underline {
    width: 100%;
    height: 1px;
    background-color: white;
    margin: 0 0 20px;

  }

  &__link {
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
    color: white;
    text-decoration: white;
  }
}

.black {
  background-color: black;
}

.yellow {
  background-color: #f79a00;
}

.main__whoisit {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 90px;

  &--info {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;

    &-title {
      margin: 0 0 30px;
      font-size: 48px;
      font-weight: 900;
      line-height: 58px;
    }

    &-about {
      margin: 0;
      font-size: 14px;
      font-weight: 400;
      line-height: 27px;
    }

  }
}

.bold {
  font-weight: 700;
}

.main__statistic {
  width: 100%;

  &--background {
    width: 100%;
    height: 258px;
    background-image: url("../images/jpg/main-info/statisc-background.jpg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    margin-bottom: 269px;
  }
}

.statistic__blocks {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  position: relative;
  top: 85px;
  margin-bottom: 90px;
}

.statistic__block {
  margin-bottom: 90px;
  background-color: white;

  &--container-yellow {
    border: 20px solid #f79a00;
    @extend %flexCenter;
    flex-direction: column;
  }

  &--container-black {
    border: 20px solid black;
    @extend %flexCenter;
    flex-direction: column;
  }
}

.statistic__number {
  margin: 78px 0 13px;
  font-size: 96px;
  font-weight: 900;
  line-height: 92px;
}

.statistic__name {
  font-size: 14px;
  font-weight: 400;
  line-height: 13px;
  margin-bottom: 77px;
}

.yellow-text {
  color: #f79a00;
}

.black-text {
  color: black;
}
